import React from 'react';
import background from '../images/backgrounds/default_bg.webp';

export default function Skills() {
  return (
    <>
			<div className='content'>
				<div 
					className='background-image'
					style={{
						backgroundImage:`url(${background})`,
						filter: `brightness(40%)`,
					}}>
				</div>
				<div className='skills-info'>
					<div className='skills-info__header'>Umiejtnoci</div>
					<div className='skills-info__skills-container'>
						<div className='skills-info__skill-item'>
							<div className='skills-info__skill-name-container'>
								<i className='skills-info__skill-icon lab la-html5'></i>
								<div className='skills-info__skill-name'>HTML5</div>
							</div>
							<div className='skills-info__skill-description'><div>Biega znajomo skadni, stosowanie nazewnictwa klas w konwecji BEM.</div></div>
						</div>
						<div className='skills-info__skill-item'>
							<div className='skills-info__skill-name-container'>
								<i className='skills-info__skill-icon lab la-css3-alt'></i>
								<div className='skills-info__skill-name'>CSS3</div>
							</div>
							<div className='skills-info__skill-description'>Biega znajomo skadni. Umiejtno tworzenia czytelnych, responsywnych i nowoczesnych interfejsw.</div>
						</div>
						<div className='skills-info__skill-item'>
							<div className='skills-info__skill-name-container'>
								<i className='skills-info__skill-icon lab la-sass'></i>
								<div className='skills-info__skill-name'>Sass</div>
							</div>
							<div className='skills-info__skill-description'>Stosowanie zagniede z nazewnictwem w konwecji BEM. Korzystanie ze zmiennych, domieszek i breakpointw Media Queries.</div>
						</div>
						<div className='skills-info__skill-item'>
							<div className='skills-info__skill-name-container'>
								<i className='skills-info__skill-icon lab la-js-square'></i>
								<div className='skills-info__skill-name'>JavaScript</div>
							</div>
							<div className='skills-info__skill-description'>Znajomo skadni ES6+. Umiejtno tworzenia skryptw zorientowanych obiektowo i korzystania z Rest API.</div>
						</div>
						<div className='skills-info__skill-item'>
							<div className='skills-info__skill-name-container'>
								<i className='skills-info__skill-icon lab la-react'></i>
								<div className='skills-info__skill-name'>ReactJS</div>
							</div>
							<div className='skills-info__skill-description'>Biega znajomo skadni. Umiejtno pracy z komponentami klasowymi i funkcyjnymi z wykorzystanie hookw. Znajomo React Router i React Context.</div>
						</div>
						<div className='skills-info__skill-item'>
							<div className='skills-info__skill-name-container'>
								<i className='skills-info__skill-icon las la-laptop-code'></i>
								<div className='skills-info__skill-name'>RWD</div>
							</div>
							<div className='skills-info__skill-description'>Umiejtno tworzenia responsywnych interfejsw z wykorzystaniem Flexboxa, CSS Grid, Media queries lub Bootstrap.</div>
						</div>
						<div className='skills-info__skill-item'>
							<div className='skills-info__skill-name-container'>
								<i className='skills-info__skill-icon icon icon-bootstrap'></i>
								<div className='skills-info__skill-name'>Bootstrap</div>
							</div>
							<div className='skills-info__skill-description'>Umiejtno korzystania z zasobw biblioteki i tworzenia responsywnego interfejsu.</div>
						</div>
						<div className='skills-info__skill-item'>
							<div className='skills-info__skill-name-container'>
								<i className='skills-info__skill-icon lab la-git'></i>
								<div className='skills-info__skill-name'>GIT</div>
							</div>
							<div className='skills-info__skill-description'>Umiejtno tworzenia i aktualizowania repozytoriw. Praca ze zdalnym repozytorium na GitHub. </div>
						</div>
						<div className='skills-info__skill-item'>
							<div className='skills-info__skill-name-container'>
								<i className='skills-info__skill-icon icon icon-visualstudiocode'></i>
								<div className='skills-info__skill-name'>VSC</div>
							</div>
							<div className='skills-info__skill-description'>Znajomo rodowiska Visual Studio Code, praca z Node Package Manager.</div>
						</div>
					</div>
					<div className='skills-info__header'>Znajomo podstaw</div>
					<div className='skills-info__skills-container'>
						<div className='skills-info__skill-item'>
							<div className='skills-info__skill-name-container'>
								<i className='skills-info__skill-icon icon icon-typescript'></i>
								<div className='skills-info__skill-name'>TypeScript</div>
							</div>
							<div className='skills-info__skill-description'>Umiejtno stosowania statycznego typowania. Znajomo typw, interfejsw i umiejtno zastosowania ich w projektach korzystajcych z ReactJS.</div>
						</div>
						<div className='skills-info__skill-item'>
							<div className='skills-info__skill-name-container'>
								<i className='skills-info__skill-icon lab la-php'></i>
								<div className='skills-info__skill-name'>PHP</div>
							</div>
							<div className='skills-info__skill-description'>Umiejtno obsugi i walidacji formularzy, korzystania z metod POST, GET i sesji. Praca z baz danych MySQL.</div>
						</div>
						<div className='skills-info__skill-item'>
							<div className='skills-info__skill-name-container'>
								<i className='skills-info__skill-icon icon icon-mysql'></i>
								<div className='skills-info__skill-name'>MySQL</div>
							</div>
							<div className='skills-info__skill-description'>Znajomo podstaw i rodowiska phpMyAdmin. Umiejtno tworzenia i modyfikowania prostych baz danych.</div>
						</div>
					</div>
					<div className='skills-info__header'>Zamierzam pozna</div>
					<div className='skills-info__skills-container'>
						<div className='skills-info__skill-item'>
							<div className='skills-info__skill-name-container'>
								<i className='skills-info__skill-icon lab la-node-js'></i>
								<div className='skills-info__skill-name'>NodeJS</div>
							</div>
							<div className='skills-info__skill-description'>W przyszoci jestem zainteresowany prac w podejciu Full-Stack, dlatego zamierzam pozna NodeJS.</div>
						</div>
					</div>
				</div>
			</div>
    </>
  );
};
