import React from 'react';
import about from '../images/backgrounds/about_me_bg.webp';
import photography from '../images/photography/photography.webp';
import cv from '../download/ukasz_Wickowski_CV.pdf';

export default function About() {
  return (
    <>
      <div className='content'>
        <div 
          className='background-image'
          style={{
            backgroundImage:`url(${about})`,
            filter: `brightness(80%)`,
          }}>
        </div>
        <div className='about'>
          <div className='about__header'>O mnie</div>
          <div className='about__info-container'>
            <div className='about__photo'>
              <img alt='photography' src={photography}/>
              <div className='about__media-links'>
                <div className='about__media-links-header'>
                  Znajdziesz mnie na
                </div>
                <div className='about__media-links-container'>
                  <a 
                    href='https://www.linkedin.com/in/WieckowskiLukasz' 
                    target='_blank' 
                    rel='noreferrer' 
                    className='about__media-link'>
                      <i className='lab la-linkedin-in'></i>
                  </a>
                  <a 
                    href='https://www.facebook.com/IWieckowskiLukasz' 
                    target='_blank' 
                    rel='noreferrer' 
                    className='about__media-link'>
                      <i className='lab la-facebook-f'></i>
                  </a>
                  <a 
                    href='https://github.com/WieckowskiLukasz' 
                    target='_blank' 
                    rel='noreferrer' 
                    className='about__media-link'>
                      <i className='lab la-github'></i>
                  </a>
                </div>
              </div>
            </div>
            <div className='about__info'>
              <p>
                Nazywam si ukasz Wickowski, od trzech lat  interesuj si wykonywaniem aplikacji internetowych.
              </p>
              <p>
                Jestem abolwentem Szkoy Wyszej im. Pawa Wodkowica w Pocku, ukoczyem studia inynierskie na kierunku <i>Informatyka</i> ze specjalnoci <i>Programowanie i bazy danych</i>.  
              </p>
              <p>
                Praktycznie od pocztku studiw moj uwag skupiem na webmasteringu, a szczeglnie na technologiach front-endowych. Moje ulubione technologie to ReactJS i Sass.
              </p>
              <p>
                Mam za sob jeden komercyjny projekt, ktry wykonaem na pocztku studiw. Obecnie poszukuj pracy na stanowisku Junior Front-End Developer.
              </p>
              <a 
                href={cv}
                target='_blank' 
                rel='noreferrer' 
                className='about__button btn'
              >Pobierz CV <i className='las la-arrow-down'></i></a>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
